.catalog-filter {
  &__accordion {
    width: 100%;
  }

  &__element {
    width: 100%;

    @include vp-1199 {
      &.is-active {
        .catalog-filter__button {
          color: $color-pumpkin;

          svg {
            color: $color-pumpkin;

            transform: rotate(180deg);
          }
        }
      }
    }
  }

  &__button {
    display: flex;
    align-items: center;
    margin: 0 0 30px;

    font-weight: 500;
    font-size: 24px;
    line-height: 34px;

    color: $color-default-black;

    transition: color $trans-default;

    svg {
      flex-shrink: 0;

      display: none;
      width: 24px;
      height: 24px;
      margin: 0 10px 0 0;
      padding: 5px;

      transition: color $trans-default, transform $trans-default;

      @include vp-1199 {
        display: block;
      }
    }

    @include vp-1199 {
      margin: 0;

      font-weight: 600;
      font-size: 18px;
      line-height: 25px;

      cursor: pointer;
      user-select: none;
    }

    @include hover-focus {
      @include vp-1199 {
        color: $color-pumpkin;

        svg {
          color: $color-pumpkin;
        }
      }
    }

    @include active {
      @include vp-1199 {
        color: $color-persimmon;

        svg {
          color: $color-persimmon;
        }
      }
    }
  }

  &__wrapper {
    padding: 30px 20px;

    border-radius: 20px;
    background-color: $color-white-smoke;

    @include vp-1199 {
      padding: 10px 0 0;

      border-radius: 0;
      background-color: $color-transparent;
    }
  }

  &__sub-accordion {
    width: 100%;
  }

  &__sub-element {
    width: 100%;
    margin: 0 0 20px;

    &:last-child {
      margin: 0;
    }

    &.is-active {
      .catalog-filter__sub-button {
        color: $color-pumpkin;

        svg {
          color: $color-pumpkin;

          transform: rotate(180deg);
        }
      }
    }
  }

  &__sub-button {
    display: flex;
    align-items: center;
    width: 100%;
    padding: 0;

    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    text-align: left;

    color: $color-nero;

    transition: color $trans-default;

    cursor: pointer;
    user-select: none;

    @include vp-1199 {
      justify-content: flex-start;

      font-size: 16px;
      line-height: 22px;
    }

    svg {
      flex-shrink: 0;

      width: 24px;
      height: 24px;
      margin: 0 10px 0 0;
      padding: 5px;

      color: $color-nero;

      transition: color $trans-default, transform $trans-default;
    }

    @include hover-focus {
      color: $color-pumpkin;

      svg {
        color: $color-pumpkin;
      }
    }

    @include active {
      color: $color-persimmon;

      svg {
        color: $color-persimmon;
      }
    }
  }

  &__sub-content {
    width: 100%;
  }

  &__content-wrapper {
    padding: 20px 0 0 5px;
  }

  &__list {
    display: flex;
    flex-direction: column;
  }

  &__item {
    margin: 0 0 10px;

    &:last-child {
      margin: 0;
    }
  }

  &__range {
    display: flex;
    justify-content: space-between;
  }

  &__input {
    width: 100%;
    max-width: 132px;
    margin: 0 10px 0 0;

    &:last-child {
      margin: 0;
    }

    input {
      width: 100%;
      padding: 4px;

      font-weight: 400;
      font-size: 16px;
      line-height: 140%;
      text-align: center;

      border: 1px solid $color-very-light-grey-2;
      border-radius: 4px;
      color: $color-nero;

      outline: none;

      &::placeholder {
        color: $color-grey;
      }
    }
  }

  &__buttons-wrapper {
    display: flex;
    flex-direction: column;
    margin: 40px 0 0;
  }

  &__show {
    width: 100%;
    margin: 0 0 20px;

    @include vp-1199 {
      max-width: 320px;
    }

    @include vp-767 {
      max-width: unset;
    }
  }

  &__reset {
    width: 100%;

    @include vp-1199 {
      max-width: 320px;
    }

    @include vp-767 {
      max-width: unset;
    }
  }
}
